import React, {useContext,useState} from "react"
import {Context} from "../context"
import Navbar from "./Navbar"
import Paypal from "../paypal/Paypal"
import {Row, Col, Card, ListGroup,ListGroupItem, Form} from "react-bootstrap"

function Checkout(){
  const value = useContext(Context)
  const [total,setTotal] = useState(value.total)
  const cart = value.cart
  const [currency,setCurrency]=useState("USD")
  const [change,setChange]=useState(false)

  const changeCurrency = (e) => {
    setCurrency(e.target.value)
    if(currency=="EUR") setTotal(total*0.89)
    if(currency=="USD") setTotal(total/0.89)
    setChange(true)
  }

  return(
    <div>
        <Row >

          <Col>
            <Card className="colCheckout mt-5">
              <Card.Title className="text-center">
                Order Summary
              </Card.Title>
              <Card.Subtitle className="text-center">
                There are {cart.length} items in the cart
              </Card.Subtitle>
              <Card.Subtitle>
                Choose currency:
              </Card.Subtitle>
              <Form.Control onChange={changeCurrency} as="select">
                <option>USD</option>
                <option>EUR</option>
              </Form.Control>
              <Card.Body>
                <Card.Text className="checkout-text text-center">
                  <ListGroup>

                      {cart.map((item,index)=>{
                          return(<ListGroupItem>
                            <span className="pr-3">{item.name}</span>
                            <span className="pr-3">-</span>
                            <span className="pr-3">{item.quantity}</span>
                            <span className="pr-3">-</span>
                            <span className="pr-3 text-primary">{item.total}$</span>


                          </ListGroupItem>)
                      })}

                  </ListGroup>
                </Card.Text>
                <Card.Text className="text-center">Total: {total}{" " + currency}</Card.Text>
                <Card.Footer>
                  <Paypal currency={currency} total={total}></Paypal>
                </Card.Footer>
              </Card.Body>
            </Card>
          </Col>

        </Row>
    </div>
  )
}

export default Checkout
